<template>
    <div class='loginDiv'>
    	<h2 class="title">广东东软学生网上选课系统</h2>
      <div class="loginWrap">
      	<ul class="typeSelect">
      		<li v-for="(item,i) in typeList" :key="i" @click="handleSelect(i)" :class="{isactive: i==activeIndex}">{{item}}</li>
      	</ul>
      	<div class="usernameInput">
      		<img src="../assets/images/icon_login_phone.png" class="img"/>
      		<input type="text" v-model="username" placeholder="请输入用户名"/>
      	</div>
      	<div class="usernameInput">
      		<img src="../assets/images/login_password.png" class="img"/>
      		<input type="password" v-model="password" placeholder="请输入密码"/>
      	</div>
      	<div class="loginIn" @click="loginMenm">登录</div>
      </div>
    </div>
</template>

<script>
import {login} from '../services/service'
import {isApiResponseOk, setSessionStorage, getSessionStorage} from '../utils/com.js'
export default {
  data () {
    return {
    	activeIndex: 0,
    	typeList: ['管理员', '学生', '教师'],
    	password: '',
    	username: ''
    }
  },
  created () {
    // this.refreshToken()
  },
  methods: {
  	handleSelect(i) {
  		this.activeIndex = i
  		this.username = ''
  		this.password = ''
    },
    loginMenm () {
    	if (!this.username || !this.password) {
    		this.$message({
    			showClose: true,
    			message: '用户名和密码不能为空',
    			type: 'warning'
    		})
    		return
    	}
    	let data = {
    		username: this.username,
    		password: this.password,
    		usertype: this.activeIndex+1
    	}
    	let that = this
    	login(data).then(function (res) {
	        if (res && res.code == 0) {
	           localStorage.setItem('access_token', res.data.token)
	           that.$store.commit('setToken', res.data.token)
	           setSessionStorage('userInfo', res.data)
	           if (res.data.userType == 1) {
	           	that.$router.push({name: 'student'})
	           } else if (res.data.userType == 2) {
	           	that.$router.push({name: 'studentSelectCourse'})
	           } else {
	           	that.$router.push({name: 'student'})
	           }
	        } else {
	         	that.$message({
		            showClose: true,
		            message: '用户名或密码错误',
		            type: 'warning'
	            })
	        }
	    }).catch(function (res) {
	        console.log(res)
	    })

//	    localStorage.setItem('access_token', this.username)
//	    	this.$store.commit('setToken', this.username)
//	    if (this.username.length == 1) {
//	      	setSessionStorage('userInfo', {userType: 1})
//	      	this.$router.push({name: 'student'})
//	    } else if (this.username.length == 2) {
//	      	setSessionStorage('userInfo', {userType: 2})
//	      	this.$router.push({name: 'student'})
//	    } else {
//		      	setSessionStorage('userInfo', {userType: 3})
//		      	this.$router.push({name: 'studentSelectCourse'})
//	    }
	}
  }
}
</script>
<style lang="sass" scoped>
.loginDiv
    background: url("../assets/images/bg_gym_login.png") no-repeat center center;
    background-size: cover;
    background-position: top right;
    width: 100%;
    height: 100%;
    color: #8597A1;
    position: relative;
    overflow: hidden;
    .title
    	color: #fff;
    	font-size: 0.4rem;
    	font-weight: 800;
    	text-align: center;
    	margin-top: 2rem;
</style>
<style>
	.loginWrap{
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%,-50%);
		width: 5rem;
		height: 3rem;
		background: #fff;
		
	}
	.typeSelect{
		width: 100%;
		height: 0.6rem;
		display: flex;
		justify-content: space-around;
		align-items: center;
		background: #666;
	}
	.typeSelect li{
		width: 100%;
		height: 0.6rem;
		text-align: center;
		line-height: 0.6rem;
		color: #fff;
		font: 0.16rem;
		cursor: pointer;
	}
	.typeSelect li.isactive{
		border-bottom: 2px solid yellow;
    color: yellow;
	}
	.usernameInput{
		display: flex;
		width: 60%;
		height: 0.6rem;
		box-sizing: border-box;
		padding-left: 0.2rem;
		align-items: center;
		margin-bottom: 0.12rem ;
		margin-left: 20%;
		border-bottom: 1px solid #666;
	}
	.usernameInput .img{
		width: 0.32rem;
		height: 0.32rem;
		margin-right: 0.2rem;
	}
	.usernameInput input{
		flex: 1;
		border: 0;
		outline: none;
		font-size: 16px;
	}
	.loginIn{
		width: 60%;
		height: 0.6rem;
		color: #fff;
		background: #20C6BA;
		cursor: pointer;
		line-height: 0.6rem;
		text-align: center;
		border-radius: 0.04rem;
		margin-left: 20%;
		margin-top: 0.3rem;
	}
</style>


